<template>
  <el-table :data="table_list" stripe style="width: 100%">
    <el-table-column prop="date" label="书名" width="180"> </el-table-column>
    <el-table-column prop="name" label="作者" width="180"> </el-table-column>
    <el-table-column prop="address" label="出版社"> </el-table-column>
  </el-table>
</template>

<script>
export default {
  data() {
    return {
      datalist: [],
      res: '',
      table_list: [],
      obj: {}
    }
  },
  created() {
    this.getdata()
  },
  methods: {
    async getdata() {
      const { data: res } = await this.$http.get('http://localhost:3000/book/list')
      this.res = res
      const y = this.$store.state.count2
      let x = 0
      let z = 0
      for (x; x < y.length; x++) {
        z = y[x]
        this.datalist.push(res[z])
      }
      console.log(this.datalist)
      let m = 0
      for (m; m < this.datalist.length; m++) {
        this.obj.date = this.datalist[m].title
        this.obj.name = this.datalist[m].author
        this.obj.address = this.datalist[m].publisher
        this.table_list.push(this.obj)
        this.obj = {}
      }
    },
    setTime() {
      var date = new Date()
      var month = date.getMonth()
      month = month + 1
      month = month < 10 ? '0' + month : month
      var datas = date.getDate()
      datas = datas < 10 ? '0' + datas : datas
      var h = date.getHours()
      h = h < 10 ? '0' + h : h
      var m = date.getMinutes()
      m = m < 10 ? '0' + m : m
    //  var s = date.getSeconds()
    //  s = s < 10 ? '0' + s : s
      return month+'/' + datas +'/' + h +'.' +m
    }
  },
  getlist() {}
}
</script>

