<template>
  <div>
    <van-row type="flex" gutter="10">
      <van-col span="8" class="m" v-for="item in datalist" :key="item.id">
        <van-card desc="" :title="item.title" :thumb="'http://192.168.8.172:3000/img' + item.cover">
          <template #tags>
            <div id="tags">
              <van-tag plain type="danger">{{ item.author }}</van-tag>
            </div>
            <div><van-tag plain type="danger">Ecomomic</van-tag></div>
          </template>
          <template #footer>
            <van-button size="mini" @click="add2(item.id)">浏览</van-button>
            <van-button size="mini" @click="collect(item.id)">收藏</van-button>
          </template>
        </van-card>
      </van-col>
    </van-row>
    <div class="block">
      <el-pagination :current-page="currentPage" @current-change="handleCurrentChange" layout="prev, pager, next" :total="50"> </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      datalist: [],
      currentPage: 1,
      j: 0
    }
  },
  created() {
    this.getdata()
  },
  methods: {
    async getdata() {
      // 生成一个随机数字
      const i = Math.ceil(Math.random() * 100) // 获取从 1 到 10 的随机整数，取 0 的概率极小。
      const { data: res } = await this.$http.get('http://192.168.8.172:3000/book/list')
      for (this.j = (this.currentPage - 1) * 9 + 100; this.j < this.currentPage * 9 +100; this.j++) {
        this.datalist.push(res[this.j])
      }
      console.log(res[i].cover)
      //点击第几页
      // this.thumb = 'http://localhost:3000/img/' + this.datalist[0].cover
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.datalist = []
      this.getdata()
      console.log(val)
    },
    add2(x) {
      this.$store.commit('add2', x - 1), 
      console.log(this.$store.state.count2)
        this.$toast('浏览记录')
    },
    collect(x) {
      this.$store.commit('add', x - 1)
      console.log(this.$store.state.count)
      this.$toast('收藏成功')
    }
  }
}
</script>

