import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
    state:{
        //收藏
        count:[13,25,10,2],
        //浏览记录
        count2:[1],
        username:''
    },
    mutations:{
        add(state,bookid) {
            state.count.push(bookid)
        },
        add2(state,bookid2) {
            state.count2.push(bookid2)
        }
    },
    actions:{

    }
})
