<template>
  <el-autocomplete v-model="state" :fetch-suggestions="querySearchAsync" placeholder="请输入内容" @select="handleSelect"></el-autocomplete>
</template>
<script>
export default {
  data() {
    return {
      restaurants: [],
      state: '',
      timeout: null
    }
  },
  methods: {
    loadAll() {
      return []
    },
    async getdata() {
      // let request = 'http://101.132.45.41:8087/NovelSystem/fuzzSearch/' + this.state

      // const { data: res } = await this.$http.post(request)
      const { data: res } = await this.$http.get('http://192.168.8.172:3000/book/list')
      //  console.log(typeof(res.data.books))
      console.log(res[0].title)
      let i = 0
      for (i = 0; i < 400; i++) {
        this.restaurants.push({ value: res[i].title })
      }
    },
    querySearchAsync(queryString, cb) {
      var restaurants = this.restaurants
      var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants
      // this.getdata()
      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        cb(results)
      }, 3000 * Math.random())
    },
    createStateFilter(queryString) {
      return state => {
        return state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
      }
    },
    handleSelect(item) {
      console.log(item)
    }
  },
  async mounted() {
    // const { data: res } = await this.$http.post(request)
    const { data: res } = await this.$http.get('http://192.168.8.172:3000/book/list')
    //  console.log(typeof(res.data.books))
    console.log(res[0].title)
    let i = 0
    for (i = 0; i < 400; i++) {
      this.restaurants.push({ value: res[i].title })
    }
  }
}
</script>

