<template>
  <div class="l">
    <div class="login_box">
      <el-form label-width="40px" :model="login_Form">
        <el-form-item label="账号">
          <el-input v-model="login_Form.username"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="login_Form.password"></el-input>
        </el-form-item>
      </el-form>
      <div class="btn">
        <el-button round @click="register">注册</el-button>
        <el-button round @click="login">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      login_Form: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    async register() {
      const { data: res } = await this.$http.post('/register', JSON.stringify(this.login_Form))
      console.log(res)
      // 判断成功与否
      // if (res !== 200) return this.$message('这是一条消息提示')
      if (res.status !== 0) return this.$message(res.message)
      // 通过编程式导航跳转
      this.$router.push('/home')
      this.$message(res.message)
    },
    async login() {
      const { data: res } = await this.$http.post('/login', JSON.stringify(this.login_Form))
      if (res.status !== 0) return this.$message(res.message)
      this.$message(res.message)
      console.log(res)
      let a = res.likes
      console.log(typeof(a))
      a = a.replace('"', '').replace('"', '')
      console.log(a)
      this.$store.state.count = a
      this.$store.state.username = this.login_Form.username
      if(this.login_Form.username == '123') return this.$router.push('/user')
      this.$router.push('/home')
    }
  }
}
</script>

