<template>
  <div>
    <van-row type="flex" gutter="10">
      <van-col span="8" class="m" v-for="item in datalist" :key="item.id">
        <van-card desc="" :title="item.titile" :thumb="'http://192.168.8.172:3000/img' + item.cover" :category="item.categoryText" :filenam="item.fileName">
          <template #tags>
            <div id="tags">
              <van-tag plain type="danger">{{ item.author }}</van-tag>
            </div>
            <div>
              <van-tag plain type="danger">{{ item.categoryText }}</van-tag>
            </div>
          </template>
          <template #footer>
            <!-- <van-button size="mini">删除</van-button> -->
            <van-button size="mini" @click="download(item.id)">下载</van-button>
          </template>
        </van-card>
        <!-- 收藏的渲染 -->
      </van-col>
    </van-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      datalist: [],
      categoryText: '',
      res:''
    }
  },
  created() {
    this.getdata()
  },
  methods: {
    async getdata() {
      // 生成一个随机数字
      const i = Math.ceil(Math.random() * 100) // 获取从 1 到 10 的随机整数，取 0 的概率极小。
      const { data: res } = await this.$http.get('http://192.168.8.172:3000/book/list')
      console.log(res)
      this.res = res
      const y = this.$store.state.count
      let x = 0
      let z = 0
      for (x; x < y.length; x++) {
        z = y[x]
        this.datalist.push(res[z])
      }
      console.log(res[i].cover)
      // this.thumb = 'http://localhost:3000/img/' + this.datalist[0].cover
    },
    download(x) {
      const l = 'http://192.168.8.172:3000/epub/' + this.res[x-1].categoryText + '/' + this.res[x-1].fileName + '.epub'
      location.href = l
      console.log(l)
      console.log(x)
    }
  }
}
</script>

