<template>
  <div class="number">
    <van-row type="flex" justify="space-around" v-for="item in datalist" :key="item.id">
      <van-col span="3" class="n1">{{ item.id }}</van-col>
      <van-col span="18">
        <van-card :desc="item.bookId" :title="item.title" :thumb="'http://localhost:3000//img' + item.cover">
          <template #tags>
            <van-tag plain type="danger">{{ item.categoryText }}</van-tag>
            <van-tag plain type="danger">{{ item.publisher }}</van-tag>
          </template>
          <template #footer>
            <van-button size="mini" @click="add2(item.id)">浏览</van-button>
            <van-button size="mini" @click="collect">收藏</van-button>
          </template>
        </van-card>
      </van-col>
    </van-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      datalist: []
    }
  },
  created() {
    this.getdata()
  },
  methods: {
    async getdata() {
      const { data: res } = await this.$http.get('http://localhost:3000/book/list')
      console.log(res[10])
      let x = 0
      for (x; x < 3; x++) {
        this.datalist.push(res[x])
      }
    },
    collect(x) {
      this.$store.commit('add', x - 1)
      console.log(this.$store.state.count)
      this.$toast('收藏成功')
    },
        add2(x) {
      this.$store.commit('add2', x - 1), 
      console.log(this.$store.state.count2)
       this.$toast('浏览记录')
    },
  }
}
</script>

<style scoped>
.n1 {
  font-size: 3.5rem;
  text-align: center;
  line-height: 8rem;
}
.van-row {
  margin-top: 1.64rem;
}
.van-card {
  border-radius: 0.32rem;
}
</style>
