<template>
  <div>
    <div class="like">
      <div class="sp1">猜你喜欢</div>
      <div class="sp2" @click="onclick">换一个</div>
    </div>
    <van-card :desc="desc" :title="title" :thumb="thumb">
      <template #tags>
        <div id="tags">
          <van-tag plain type="danger">{{ pub }}</van-tag>
        </div>
        <div class="t2">
          <van-tag plain type="danger">{{ category }}</van-tag>
        </div>
      </template>
      <template #footer>
        <div class="btn">
          <el-button :plain="true" @click="add">详情</el-button>
         <el-button :plain="true" @click="open2">收藏</el-button>
        </div>
      </template>
    </van-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: '',
      desc: '',
      pub: '',
      category: '',
      thumb: 'http://localhost:3000/img/History/978-3-319-92964-4_CoverFigure.jpg',
      id: ''
    }
  },
  created() {
    this.getdata()
  },
  methods: {
    async getdata() {
      // 生成一个随机数字
      const i = Math.ceil(Math.random() * 100) // 获取从 1 到 10 的随机整数，取 0 的概率极小。
      const { data: res } = await this.$http.get('http://192.168.8.172:3000/book/list')
      this.title = res[i].title
      this.desc = res[i].author
      this.pub = res[i].publisher
      ;(this.category = res[i].categoryText), console.log(res[i].cover)
      this.thumb = 'http://192.168.8.172:3000/img/' + res[i].cover
      this.id = res[i].id
    },
    onclick() {
      this.getdata()
    },
    open2() {
      this.$store.commit('add', this.id-1)
      console.log(this.$store.state.count)
      this.$message({
          message: '收藏成功',
          type: 'success'
      })
    },
    add() {
          this.$store.commit('add2', this.id - 1), 
      console.log(this.$store.state.count2)
    }
  }
}
</script>

