import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home/Home.vue'
import Search from '../views/Search/Search.vue'
import User from '../views/User/user.vue'
import Like from '../views/like/Like.vue' 
import Login from '../views/Login/login.vue'
import C1 from '../components/category/C1.vue'
import C2 from '../components/category/C2.vue'
import C3 from '../components/category/C3.vue'
import C4 from '../components/category/C4.vue'
import C5 from '../components/category/C5.vue'
import C6 from '../components/category/C6.vue'
import B2 from '../components/category/B2.vue'
import B3 from '../components/category/B3.vue'

Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    component: Login
  },
  {
    path:'/login',
    component: Login
  },
  {
    path: '/home',
    component: Home
  },
  {
    path:'/search',
    component: Search,
    redirect:'/1-1',
    children:[
      {
        path:'/1-1',
        component: C1
      },
      {
      path:'/1-2',
      component: C2
    },
    {
      path:'/1-3',
      component: C3
    },
    {
      path:'/1-4',
      component: C4
    },
    {
      path:'/1-5',
      component: C5
    },
    {
      path:'/1-6',
      component: C6,
    },
    {
      path:'/2',
      component:B3
    },
    {
      path:'/3',
      component:B2
    }
    ]
  },
 {
    path:'/user',
    component: User
  },
  {
    path:'/like',
    component: Like
  }
  
]



const router = new VueRouter({
  routes
})

export default router
